.lineup {
    background-color: $rosa;
    padding: $separacion 0;
    margin: $separacion 0;
    h3 {
        color: $blanco;
        font-size: 4rem;
        text-align: center;
        font-weight: $black;
    }
    .dia {
        color: $blanco;
        text-align: center;
        margin: $separacion 0;
        font-weight: $bold;
        font-size: 2.5rem;
        padding: 2rem 0;
        background: rgb(117, 47, 151);
        background: linear-gradient(90deg, rgba(117, 47, 151, 1) 0%, rgba(74, 60, 206, 1) 50%, rgba(117, 47, 151, 1) 100%);
    }
}

.escenarios-contenedor {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    @include mediaTablet {
        display: grid;
        grid-template-columns: repeat(2, calc(48% - 2.5rem));
        justify-content: space-around;
    }
}

.nombre-escenario {
    text-align: center;
    font-weight: $bold;
    padding-top: 3rem;
    &:after {
        content: '';
        display: block;
        width: 15rem;
        height: 10rem;
        margin: 2rem auto 0;
        background-size: 15rem;
        background-repeat: no-repeat;
        background-position: center center;
    }
}

.edm .nombre-escenario {
    &:after {
        background-image: url(../img/icono_edm.png);
    }
}

.rock .nombre-escenario {
    &:after {
        background-image: url(/build/img/icono_rock.png);
    }
}

ul.lista-horarios {
    padding: 0;
}

.bg-amarillo {
    @include tarjetaEscenario($amarillo, $amarilloSecundario) {}
}

.bg-verde {
    @include tarjetaEscenario($verde, $verdeSecundario) {}
}

.horario span {
    font-weight: $regular;
    &:before {
        content: '\07c';
        margin-right: 0.5rem;
        font-weight: $black;
    }
}