        .boletos {
            background-color: $verde;
            margin: $separacion 0;
            h3 {
                text-align: center;
                padding: $separacion/1.5 0 $separacion 0;
                color: $blanco;
            }
        }
        
        @media(max-width:768px) {
            .contenedor {
                width: 80%;
            }
        }
        
        .contenedor-boletos {
            padding-bottom: 2rem;
            @include gridColumn(1, 0);
            justify-content: center;
            @include mediaTablet {
                @include gridColumn(2, $separacion/2);
                justify-items: stretch;
            }
        }
        
        .plan {
            &:nth-child(2) {
                order: -1;
            }
            margin-bottom: $separacion;
            p {
                margin: 0 0 $separacion/2 0;
                color: $blanco;
                font-size: 3rem;
                text-align: center
            }
        }
        
        .pase {
            width: 100%;
            border-spacing: 0;
            thead {
                color: $blanco;
                font-weight: $delgada;
                background-color: $rosa;
                th {
                    padding: $separacion/4 0;
                    font-weight: $delgada;
                    text-transform: uppercase;
                }
            }
            tbody {
                font-weight: $regular;
                background-color: $blanco;
                td {
                    border-bottom: 1px solid #979797;
                    padding: $separacion/4 0;
                    font-weight: $regular;
                    padding-left: 5rem;
                    font-size: 2rem;
                }
                tr {
                    &:last-of-type {
                        td {
                            border: none;
                        }
                    }
                }
            }
            tfoot {
                background-color: $morado;
                td {
                    text-align: center;
                    font-size: 3rem;
                    color: $blanco;
                    font-weight: $bold;
                    padding: $separacion/4 0;
                    text-transform: uppercase;
                }
            }
        }