html {
  font-size: 62.5%;
  box-sizing: border-box; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem; }
  body.fijar-body {
    overflow: hidden; }

p {
  font-size: 2rem;
  margin: 0; }

.contenedor {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto; }

a {
  text-decoration: none;
  font-size: 2rem; }

img {
  max-width: 100%; }

h1 {
  font-size: 4rem; }

h2 {
  font-size: 3.6rem; }

h3 {
  font-size: 3rem; }

h1,
h2,
h3 {
  margin: 0; }

video {
  max-width: 100%; }

.ScrollViewMargin {
  height: 11rem; }

.header {
  background-color: #4CB8B3; }
  .header.fijo {
    position: fixed;
    width: 100%;
    top: 0;
    -webkit-box-shadow: 0px 7px 11px -5px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 0px 7px 11px -5px rgba(0, 0, 0, 0.75);
    box-shadow: 0px 7px 11px -5px rgba(0, 0, 0, 0.75); }
  .header .contenido-header {
    padding: 2.5rem 0; }
    @media (min-width: 768px) {
      .header .contenido-header {
        display: flex;
        justify-content: space-between;
        align-items: center; } }
  .header h1 {
    color: #ffffff;
    margin-bottom: 1rem;
    text-align: center; }
    @media (min-width: 768px) {
      .header h1 {
        margin: 0; } }

.navegacion-principal {
  text-align: center; }
  .navegacion-principal a {
    color: #ffffff;
    margin-left: 1rem; }
    .navegacion-principal a:hover {
      color: #FEF46D;
      cursor: pointer; }

.video {
  position: relative;
  overflow-y: hidden;
  max-height: 50rem; }
  .video .overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#891c1e+0,9e37f2+67,ffffff+100&0.8+0,0.5+100 */
    background: -moz-linear-gradient(-45deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.6) 67%, rgba(255, 255, 255, 0.5) 100%);
    /* FF3.6-15 */
    background: -webkit-linear-gradient(-45deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.6) 67%, rgba(255, 255, 255, 0.5) 100%);
    /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(135deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.6) 67%, rgba(255, 255, 255, 0.5) 100%); }
  .video video {
    height: 100%;
    display: block;
    width: 100%;
    background-position: top top; }

.contenido-video {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  height: 100%;
  color: #ffffff; }
  .contenido-video h2 {
    font-size: 8vw; }
  .contenido-video span {
    display: inline-block;
    margin-top: 0.3rem;
    background-color: #F53756;
    padding: 0.4rem 1rem;
    font-weight: 700;
    border-radius: 2px;
    font-size: 4vw; }
  @media (min-width: 768px) {
    .contenido-video {
      align-items: flex-start; }
      .contenido-video h2 {
        font-size: 6rem; }
      .contenido-video span {
        font-size: 2rem; } }

.sobre-festival {
  margin: 2.5rem auto; }
  @media (min-width: 960px) {
    .sobre-festival {
      display: grid;
      grid-template-columns: repeat(2, calc(50% - 1.5rem));
      justify-content: space-between; } }

.contenido-festival h2 {
  margin: 1rem 0 3.33333rem; }

.contenido-festival span {
  display: inline-block;
  color: #F53756;
  margin-bottom: 1rem;
  font-weight: 700; }

.contenido-festival p {
  line-height: 1;
  font-size: 2.2rem; }

@media (min-width: 1200px) {
  .contenido-festival h2 {
    margin: 1rem 0 5rem; }
  .contenido-festival span {
    font-size: 2rem;
    margin-bottom: 2.5rem; }
  .contenido-festival p {
    font-size: 2.5rem; } }

.lineup {
  background-color: #F53756;
  padding: 5rem 0;
  margin: 5rem 0; }
  .lineup h3 {
    color: #ffffff;
    font-size: 4rem;
    text-align: center;
    font-weight: 900; }
  .lineup .dia {
    color: #ffffff;
    text-align: center;
    margin: 5rem 0;
    font-weight: 700;
    font-size: 2.5rem;
    padding: 2rem 0;
    background: #752f97;
    background: linear-gradient(90deg, #752f97 0%, #4a3cce 50%, #752f97 100%); }

.escenarios-contenedor {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }
  @media (min-width: 768px) {
    .escenarios-contenedor {
      display: grid;
      grid-template-columns: repeat(2, calc(48% - 2.5rem));
      justify-content: space-around; } }

.nombre-escenario {
  text-align: center;
  font-weight: 700;
  padding-top: 3rem; }
  .nombre-escenario:after {
    content: '';
    display: block;
    width: 15rem;
    height: 10rem;
    margin: 2rem auto 0;
    background-size: 15rem;
    background-repeat: no-repeat;
    background-position: center center; }

.edm .nombre-escenario:after {
  background-image: url(../img/icono_edm.png); }

.rock .nombre-escenario:after {
  background-image: url(/build/img/icono_rock.png); }

ul.lista-horarios {
  padding: 0; }

.bg-amarillo {
  background-color: #FEF46D;
  min-width: 85%;
  margin: 2rem 0;
  -webkit-box-shadow: 10px 10px 7px -3px rgba(0, 0, 0, 0.35);
  -moz-box-shadow: 10px 10px 7px -3px rgba(0, 0, 0, 0.35);
  box-shadow: 10px 10px 7px -3px rgba(0, 0, 0, 0.35); }
  .bg-amarillo li {
    list-style: none;
    padding: 2rem 2rem;
    font-weight: 900; }
    .bg-amarillo li:nth-child(odd) {
      background-color: #c0b840; }

.bg-verde {
  background-color: #4CB8B3;
  min-width: 85%;
  margin: 2rem 0;
  -webkit-box-shadow: 10px 10px 7px -3px rgba(0, 0, 0, 0.35);
  -moz-box-shadow: 10px 10px 7px -3px rgba(0, 0, 0, 0.35);
  box-shadow: 10px 10px 7px -3px rgba(0, 0, 0, 0.35); }
  .bg-verde li {
    list-style: none;
    padding: 2rem 2rem;
    font-weight: 900; }
    .bg-verde li:nth-child(odd) {
      background-color: #39918c; }

.horario span {
  font-weight: 400; }
  .horario span:before {
    content: '\07c';
    margin-right: 0.5rem;
    font-weight: 900; }

.galeria {
  margin: 5rem auto; }
  .galeria h3 {
    text-align: center;
    margin: 0 0 5rem 0; }

li.imagen {
  list-style: none;
  padding: 0; }

.lista-galeria {
  text-align: center;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem; }
  @media (min-width: 768px) {
    .lista-galeria {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      gap: 1rem; } }
  @media (min-width: 960px) {
    .lista-galeria {
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      gap: 1.66667rem; } }
  @media (min-width: 1200px) {
    .lista-galeria {
      display: grid;
      grid-template-columns: repeat(6, 1fr);
      gap: 1.25rem;
      justify-content: space-around; } }

.overlay {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  top: 0;
  display: grid;
  place-items: center; }
  .overlay img {
    width: 90%; }
    @media (min-width: 768px) {
      .overlay img {
        width: 70%; } }
    @media (min-width: 1200px) {
      .overlay img {
        width: 60%; } }
  .overlay p {
    display: grid;
    place-items: center;
    border-radius: 50%;
    background-color: #ffffff;
    border: 1px solid #e1e1e1;
    width: 5rem;
    height: 5rem;
    cursor: pointer; }

.boletos {
  background-color: #4CB8B3;
  margin: 5rem 0; }
  .boletos h3 {
    text-align: center;
    padding: 3.33333rem 0 5rem 0;
    color: #ffffff; }

@media (max-width: 768px) {
  .contenedor {
    width: 80%; } }

.contenedor-boletos {
  padding-bottom: 2rem;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 0;
  justify-content: center; }
  @media (min-width: 768px) {
    .contenedor-boletos {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: 2.5rem;
      justify-items: stretch; } }

.plan {
  margin-bottom: 5rem; }
  .plan:nth-child(2) {
    order: -1; }
  .plan p {
    margin: 0 0 2.5rem 0;
    color: #ffffff;
    font-size: 3rem;
    text-align: center; }

.pase {
  width: 100%;
