  .pase tbody {
    font-weight: 400;
    background-color: #ffffff; }
    .pase tbody td {
      border-bottom: 1px solid #979797;
      padding: 1.25rem 0;
      font-weight: 400;
      padding-left: 5rem;
      font-size: 2rem; }
    .pase tbody tr:last-of-type td {
