      padding: 1.25rem 0;
      text-transform: uppercase; }

.footer {
  background-color: #F53756;
  padding: 1.25rem 0; }
  .footer p {
    text-align: center;
    color: #ffffff;
    font-weight: 300;
    line-height: 1.3; }
