.galeria {
    margin: $separacion auto;
    h3 {
        text-align: center;
        margin: 0 0 $separacion 0;
    }
}

li.imagen {
    list-style: none;
    padding: 0;
}

.lista-galeria {
    text-align: center;
    padding: 0;
    @include gridColumn(2, $separacion / 5);
    @include mediaTablet {
        @include gridColumn(3, $separacion / 5);
    }
    @include mediaTablet2 {
        @include gridColumn(4, $separacion / 3);
    }
    @include mediaDesktop {
        @include gridColumn(6, $separacion/4);
        justify-content: space-around;
    }
}

.overlay {
    position: fixed;
    background-color: rgba($color: $negro, $alpha: .5);
    width: 100%;
    height: 100%;
    top: 0;
    display: grid;
    place-items: center;
    img {
        width: 90%;
        @include mediaTablet {
            width: 70%;
        }
        @include mediaDesktop {
            width: 60%;
        }
    }
    p {
        display: grid;
        place-items: center;
        border-radius: 50%;
        background-color: $blanco;
        border: 1px solid #e1e1e1;
        width: 5rem;
        height: 5rem;
        cursor: pointer;
    }
}