.sobre-festival {
    @include mediaTablet2 {
        display: grid;
        grid-template-columns: repeat(2, calc(50% - 1.5rem));
        justify-content: space-between;
    }
    margin: $separacion/2 auto;
}

.contenido-festival {
    h2 {
        margin: 1rem 0 $separacion/1.5;
    }
    span {
        display: inline-block;
        color: $rosa;
        margin-bottom: 1rem;
        font-weight: $bold;
    }
    p {
        line-height: 1;
        font-size: 2.2rem;
    }
    @include mediaDesktop {
        h2 {
            margin: 1rem 0 $separacion;
        }
        span {
            font-size: 2rem;
            margin-bottom: 2.5rem;
        }
        p {
            font-size: 2.5rem;
        }
    }
}