@mixin mediaTelefono {
    @media (min-width: $telefono) {
        @content;
    }
}

@mixin mediaTablet {
    @media (min-width: $tablet) {
        @content;
    }
}

@mixin mediaTablet2 {
    @media (min-width: $tablet2) {
        @content;
    }
}

@mixin mediaDesktop {
    @media (min-width: $desktop) {
        @content;
    }
}

@mixin tarjetaEscenario($color, $colorSecundario) {
    background-color: $color;
    min-width: 85%;
    margin: 2rem 0;
    li {
        list-style: none;
        padding: 2rem 2rem;
        font-weight: $black;
        &:nth-child(odd) {
            background-color: $colorSecundario;
        }
    }
    -webkit-box-shadow: 10px 10px 7px -3px rgba(0, 0, 0, 0.35);
    -moz-box-shadow: 10px 10px 7px -3px rgba(0, 0, 0, 0.35);
    box-shadow: 10px 10px 7px -3px rgba(0, 0, 0, 0.35);
    @content;
}

@mixin gridColumn ($colum, $espaciado) {
    display: grid;
    grid-template-columns: repeat($colum, 1fr);
    gap: $espaciado;
}