.header {
    &.fijo {
        position: fixed;
        width: 100%;
        top: 0;
        -webkit-box-shadow: 0px 7px 11px -5px rgba(0, 0, 0, 0.75);
        -moz-box-shadow: 0px 7px 11px -5px rgba(0, 0, 0, 0.75);
        box-shadow: 0px 7px 11px -5px rgba(0, 0, 0, 0.75);
    }
    background-color: $verde;
    .contenido-header {
        padding: $separacion/2 0;
        @include mediaTablet {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
    }
    h1 {
        color: $blanco;
        margin-bottom: 1rem;
        text-align: center;
        @include mediaTablet {
            margin: 0;
        }
    }
}

.navegacion-principal {
    text-align: center;
    a {
        color: $blanco;
        margin-left: 1rem;
        &:hover {
            color: $amarillo;
            cursor: pointer;
        }
    }
}