.video {
    position: relative;
    overflow-y: hidden;
    max-height: 50rem;
    .overlay {
        position: absolute;
        width: 100%;
        height: 100%;
        /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#891c1e+0,9e37f2+67,ffffff+100&0.8+0,0.5+100 */
        background: -moz-linear-gradient(-45deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.6) 67%, rgba(255, 255, 255, 0.5) 100%);
        /* FF3.6-15 */
        background: -webkit-linear-gradient(-45deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.6) 67%, rgba(255, 255, 255, 0.5) 100%);
        /* Chrome10-25,Safari5.1-6 */
        background: linear-gradient(135deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.6) 67%, rgba(255, 255, 255, 0.5) 100%);
    }
    video {
        height: 100%;
        display: block;
        width: 100%;
        background-position: top top;
    }
}

.contenido-video {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    height: 100%;
    color: $blanco;
    h2 {
        font-size: 8vw;
    }
    span {
        display: inline-block;
        margin-top: 0.3rem;
        background-color: $rosa;
        padding: 0.4rem 1rem;
        font-weight: $bold;
        border-radius: 2px;
        font-size: 4vw;
    }
    @include mediaTablet {
        align-items: flex-start;
        h2 {
            font-size: 6rem;
        }
        span {
            font-size: 2rem;
        }
    }
}